<template>
  <div class="changeLoginPsw">
    <Modal
      v-model="dialog.value"
      @on-ok="ok('form')"
      :loading="loading"
      @on-visible-change="changeVisible"
      title="修改当前登录密码"
    >
      <div class="form">
        <Form
          ref="form"
          :model="formData"
          :rules="form_rules"
          label-position="left"
          :label-width="80"
        >
          <FormItem prop="password" label="原密码">
            <Input type="password" v-model="formData.password"/>
          </FormItem>
          <FormItem prop="newPassword" label="新密码">
            <Input type="password" v-model="formData.newPassword"/>
          </FormItem>
          <FormItem prop="newPassword1" label="新密码">
            <Input type="password" v-model="formData.newPassword1"/>
          </FormItem>
        </Form>
      </div>
    </Modal>
  </div>
</template>

<script>
import { changeMyLoginPsw } from "@/api/changeMyLoginPsw";
export default {
  namel: "changeLoginPsw",
  props: {
    dialog: Object
  },
  data() {
    return {
      loading: true,
      formData: {
        password: "",
        newPassword: "",
        newPassword1: ""
      },
      form_rules: {
        password: [
          { required: true, message: "请输入用户密码", trigger: "blur" },
          {
            type: "string",
            min: 4,
            max: 8,
            message: "用户名为4-8个字符",
            trigger: "blur"
          }
        ],
        newPassword: [
          { required: true, message: "请输入新的密码", trigger: "blur" },
          {
            type: "string",
            min: 4,
            max: 8,
            message: "用户名为4-8个字符",
            trigger: "blur"
          }
        ],
        newPassword1: [
          { required: true, message: "请再次输入新的密码", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              if (value == this.formData.newPassword) {
                callback();
              } else {
                callback(new Error("两次输入的新密码不同"));
              }
            },
            message: "两次输入的新密码不同",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    ok(form) {
      setTimeout(() => {
        this.loading = false;
        this.$nextTick(() => {
          this.loading = true;
        });
      }, 1000);
      //验证
      this.$refs[form].validate(vali => {
        if (vali) {
          changeMyLoginPsw(this.formData).then(({ data }) => {
            console.log(data);
            const { code, msg } = data;
            if (code == 0) {
              this.$Message.success({
                content: `<span style='color:red'>密码</span>修改成功！请重新登录`,
                onClose: () => {
                  this.$emit("outLogin");
                }
              });
              this.dialog.value = false;
            } else {
              this.$Message.error(msg);
              this.dialog.value = false;
            }
          });
        }
      });
    },
    changeVisible(v) {
      if (!v) {
        this.$refs["form"].resetFields();
      }
    }
  }
};
</script>

<style scoped>
.form {
  padding: 0 80px;
}
</style>