<template>
  <Layout class="admin-layout">
    <Header class="header">
      <Dropdown @on-click="handleCommand" class="loginMsg">
        <a style="color:#474848" href="javascript:void(0)">
          <Icon type="md-person" color="#474848"/>
          {{userInfo?userInfo.username:''}}
          <Icon type="ios-arrow-down"></Icon>
        </a>
        <DropdownMenu slot="list">
          <DropdownItem name="login_change">修改密码</DropdownItem>
          <!-- 修改密码模态框 -->
          <pswModal @outLogin="logout" :dialog="dialog"/>
          <DropdownItem name="login_out">安全退出</DropdownItem>
        </DropdownMenu>
      </Dropdown>
    </Header>
    <Layout>
      <!-- <Sider hide-trigger> -->
      <Sider collapsible :collapsed-width="150" v-model="isCollapsed">
        <Menu width="auto" :class="menuitemClasses" theme="dark">
          <div v-for="item in menuList " :key="item.menuId">
            <Submenu v-if="item.type===0" :name="item.name">
              <template slot="title">
                <Icon type="ios-paper"/>
                {{item.name}}
              </template>
              <MenuItem v-for="child in item.children" :key="child.menuId" :name="child.menuId">
                <div @click="$router.push(`/${child.url}`)">{{child.name}}</div>
              </MenuItem>
            </Submenu>
          </div>
        </Menu>
      </Sider>
      <!-- </Sider> -->
      <Content class="content">
        <router-view></router-view>
      </Content>
    </Layout>
  </Layout>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const loginMsg = createNamespacedHelpers("loginMsg");
const menuList = createNamespacedHelpers("menuList");
// import { menuList } from "@/api/menuList";
// import { deepMeun } from "@/utils/deepMenu";
import pswModal from "@/components/changeLoginPsw";
// import { userInfo } from "os";
export default {
  data() {
    return {
      // menu: [],
      dialog: {
        value: false
      },
      isCollapsed: false
    };
  },
  components: { pswModal },
  computed: {
    // menuList() {
    //   return deepMeun(this.menu, 0);
    // },
    menuitemClasses: function() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    },
    ...loginMsg.mapGetters(["userInfo"]),
    ...menuList.mapGetters(["menuList"])
  },
  created() {
    // const res = await menuList();
    // console.log(res);
    // this.menu = res.data;
    this.$store.dispatch("loginMsg/USER_MSG");
    // console.log(this.userInfo);
    this.GET_MENU();

    // const result = await userInfo();
    // const { user, code, msg } = result.data;
    // if (code === 0) {
    //   this.user = user;
    // } else if (code === 401) {
    //   this.$Message.error({
    //     content: msg,
    //     closable: true,
    //     onClose: () => {
    //       localStorage.removeItem("token");
    //       this.$router.replace("/login");
    //     }
    //   });
    // }
  },
  methods: {
    handleCommand(command) {
      switch (command) {
        case "login_change":
          this.changePsw(); //修改密码
          break;
        case "login_out":
          this.logout(); //退出登录
          break;
      }
    },
    changePsw() {
      // console.log("修改密码");
      this.dialog.value = true;
    },
    logout() {
      // localStorage.removeItem("token", "");
      // this.$router.replace("/login");
      this.$store.dispatch("loginMsg/LOGIN_OUT");
      this.$router.replace("/login");
    },
    ...menuList.mapActions(["GET_MENU"])
  }
};
</script>

<style lang='less' scoped>
.admin-layout {
  height: 100%;
  overflow: hidden;
  .header {
    background: #ffffff;
    .loginMsg {
      float: right;
    }
  }
  .content {
    height: 100%;
    overflow: hidden;
    padding: 10px;
  }
}
</style>